import { useState, useMemo } from 'react';
import { PencilIcon } from '@heroicons/react/solid';
import clsx from 'clsx';

import EditableTable from '../../components/table/EditableTable';
import Button from '../../components/button/Button';
import IconButton from '../../components/button/IconButton';

const COLUMNS = ({ setData }) => [
  {
    Header: '',
    accessor: 'item',
    className: 'w-[18%]',
  },
  {
    Header: '',
    accessor: 'baseYear',
    editable: true,
    className: 'w-[18%]',
  },
  {
    Header: 'Target ',
    accessor: 'target',
    editable: true,
    className: 'w-[18%]',
  },
  {
    Header: '2021 Target',
    accessor: '2021',
    editable: true,
    className: 'w-[18%]',
  },
  {
    Header: '',
    accessor: 'unit',
    className: 'w-[18%]',
  },
  {
    Header: '',
    id: 'action',
    className: 'w-[10%]',
    Cell: (cell) => {
      return cell.row.original.editing ? (
        <Button
          onClick={() =>
            setData((prev) =>
              prev.map((r, i) => ({
                ...r,
                ...(i === cell.row.index && { editing: false }),
              }))
            )
          }>
          
        </Button>
      ) : (
        <IconButton
          onClick={() =>
            setData((prev) =>
              prev.map((r, i) => ({
                ...r,
                editing: i === cell.row.index,
                ...(i !== cell.row.index && { editing: r.editing || false }),
              }))
            )
          }>
          <PencilIcon className="w-5 h-5" />
        </IconButton>
      );
    },
  },
];

const DATA = [
  { item: '', baseYear: '2016', target: ' 4.2 %', 2021: '241,231', unit: '' },
  { item: '', baseYear: '-', target: ' > 60 %', 2021: '-', unit: '%' },
  {
    item: '',
    baseYear: '-',
    target: ' 2 %',
    2021: '100',
    unit: ' / ',
  },
  {
    item: '',
    baseYear: '2016',
    target: ' 1.8 %',
    2021: '80',
    unit: ' / ',
  },
  { item: '', baseYear: '-', target: ' 3 %', 2021: '100', unit: ' / ' },
  {
    item: '',
    baseYear: '2018',
    target: ' 2 %',
    2021: '50.4',
    unit: ' / ',
  },
];

export default function Goal({ className }) {
  const [data, setData] = useState(() => DATA);
  const columns = useMemo(() => COLUMNS({ setData }), []);
  const updateMyData = (rowIndex, columnId, value) => {
    setData((old) =>
      old.map((row, index) => {
        if (index === rowIndex) {
          return {
            ...old[rowIndex],
            [columnId]: value,
          };
        }

        return row;
      })
    );
  };

  return (
    <div className={clsx('w-full shadow overflow-auto rounded-t-lg space-y-2', className)}>
      <EditableTable
        columns={columns}
        data={data}
        updateMyData={updateMyData}
        getRowProps={() => ({ className: 'border-b border-divider' })}
      />
    </div>
  );
}
